	module.exports = {"data":{"site":{"siteMetadata":{"author":"---","defaultTitle":"Capstone","defaultDescription":"---"}},"markdownRemark":{"html":"<p>I knew of this club since last year, but never bothered to check it out until this year because I wasn't really feeling for joining clubs then. I still don't feel for it, but I found the experience of dropping in to be a fun time socializing with friends on campus. I hadn't had any other club experiences otherwise. I knew a lot of people from this club to begin with because I have a network of friends in CS (Computer Science) who are a part of it, so it wasn't about meeting people per se (especially with the number of introverts there), but more about learning how a club operates and even what to expect. This club rarely meets, so there's not much opportunity in getting together, but when they do, it's typically a free pizza party which I was especially down for. Free food and hanging out with friends is always great in my books. It was nice to partake in this on campus within a very niche club.</p>","excerpt":"I knew of this club since last year, but never bothered to check it out until this year because I wasn't really feeling for joining clubs…","timeToRead":1,"fields":{"date":"October 27, 2018","slug":"/journal/campus-link-3/"},"frontmatter":{"title":"Campus Link: Campus Club, Linux User Group"}}},"pathContext":{"slug":"/journal/campus-link-3/"}}

